h3{text-align:left;color:#75757B;font-size:16px;padding:0 10px;width:90%}.audio-player{background:#303038;margin:10px;flex-grow:2;border-radius:10px;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;box-sizing:border-box;border:5px solid #303038;box-shadow:-5px -5px 5px rgba(255,255,255,0.05),inset -5px -5px 5px rgba(255,255,255,0.05),5px 5px 5px rgba(0,0,0,0.3),inset 5px 5px 5px rgba(0,0,0,0.3)}#track-info{font-size:28px;margin:10px;text-align:center}#play-pause-stop-flex{display:flex;flex-direction:row;flex-wrap:no-wrap;margin:10px auto 20px auto}.music-icon{width:40px}.music-button{height:60px;background:none;margin:auto 20px;border-radius:50%;border:none;border:3px solid #303038;box-shadow:-5px -5px 5px rgba(255,255,255,0.05),inset -5px -5px 5px rgba(255,255,255,0.05),5px 5px 5px rgba(0,0,0,0.3),inset 5px 5px 5px rgba(0,0,0,0.3)}.music-button:active{border:3px solid #303038;box-shadow:-2px -2px 2px rgba(255,255,255,0.05),inset -2px -2px 2px rgba(255,255,255,0.05),2px 2px 2px rgba(0,0,0,0.3),inset 2px 2px 2px rgba(0,0,0,0.3)}

.completion-style{background:#303038;min-width:324px;border-radius:10px;display:flex;flex-direction:column;margin:10px;padding-bottom:10px;flex-grow:2;border:5px solid #303038;box-shadow:-5px -5px 5px rgba(255,255,255,0.05),inset -5px -5px 5px rgba(255,255,255,0.05),5px 5px 5px rgba(0,0,0,0.3),inset 5px 5px 5px rgba(0,0,0,0.3)}#progress-container{width:150px;height:150px;margin:20px auto}

.end-workout{width:60%;max-width:400px;height:46px;background:#ED747D;color:#303038;border:none;font-size:20px;font-weight:bold;border-radius:5px;margin:10px auto;font-family:'Varela Round', sans-serif;line-height:46px}.end-workout:hover{background:#BF5C63;box-shadow:inset 4px 4px 4px rgba(255,255,255,0.1);transition:all 0.2s ease-in-out}@media only screen and (max-width: 450px){.end-workout{width:auto;min-width:280px;margin:0px}}

.current-exercise{box-sizing:border-box;background:#303038;width:auto;min-width:75vw;flex-grow:2;margin:10px;border-radius:10px;display:flex;flex-direction:column;justify-content:center;text-align:center;padding:40px auto;border:5px solid #303038;box-shadow:-5px -5px 5px rgba(255,255,255,0.05),inset -5px -5px 5px rgba(255,255,255,0.05),5px 5px 5px rgba(0,0,0,0.3),inset 5px 5px 5px rgba(0,0,0,0.3)}#exercise-progress-container{box-sizing:border-box;width:240px;height:240px;margin:20px auto;display:flex;align-content:center;align-items:center;flex-direction:column;border:10px solid #303038;box-shadow:-10px -10px 10px rgba(255,255,255,0.05),inset -10px -10px 10px rgba(255,255,255,0.05),10px 10px 10px rgba(0,0,0,0.3),inset 10px 10px 10px rgba(0,0,0,0.3);border-radius:50%}#exercise-progress-container-inner{width:200px;height:200px;box-shadow:3px 3px 3px rgba(0,0,0,0.3),-3px -3px 3px rgba(255,255,255,0.1);border-radius:50%;margin:10px auto;background:rgba(0,0,0,0.2)}@media only screen and (max-width: 450px){.current-exercise{margin-top:20px;margin-bottom:10px;border:none;box-shadow:none;min-height:70vh}.current-exercise-text{font-size:50px;padding:10px;color:white}.next-exercise-text{font-size:24px;color:white}}@media only screen and (max-width: 900px) and (orientation: landscape){.current-exercise{min-width:80vw;max-height:90vh}.current-exercise-text{font-size:40px;padding:10px;color:white;margin:0px auto}.next-exercise-text{font-size:16px;color:white;margin:0px auto}}

.mobile-options-flex{display:flex;flex-direction:column;flex-wrap:nowrap;width:100%}

.loader-main{margin:0px auto;text-align:center}.loader{width:50vw;height:10px;border:solid 2px white;margin:0px auto;border-radius:10px;-webkit-animation:load-animation 5s linear 0s 1 forwards;animation:load-animation 5s linear 0s 1 forwards}.loader-countdown{font-size:48px}@-webkit-keyframes load-animation{0%{box-shadow:inset 0px 0 0 0.01px white}100%{box-shadow:inset 50vw 0 0 0 white}}@keyframes load-animation{0%{box-shadow:inset 0px 0 0 0.01px white}100%{box-shadow:inset 50vw 0 0 0 white}}

.loader-header-style{text-align:center;font-size:60px}

.app-container{background:#27272f;color:white;width:100vw;min-height:100vh;margin:0;padding:0}.leave-app{color:#A3A3A7;font-size:23px;background:none;font-weight:bold;border:none;display:flex;flex-direction:row;align-items:center;margin-left:20px;font-family:'Varela Round', sans-serif;padding-top:10px}.leave-app-arrow{width:5px;height:5px;border-left:solid 3px grey;border-bottom:solid 3px grey;-webkit-transform:rotate(45deg);transform:rotate(45deg);margin-right:10px}.router-link{text-decoration:none}.app-logo{position:absolute;top:1vh;right:3vw;max-width:30px;cursor:default}h1{margin:10px auto;font-size:50px}button{cursor:pointer}@media only screen and (max-width: 450px){.app-container{padding-top:00px}h1{margin:0px;font-size:16px;color:#A3A3A7}.leave-app{font-size:10px;padding-top:10px}.leave-app-arrow{width:3px;height:3px;border-left:solid 1px grey;border-bottom:solid 1px grey;-webkit-transform:rotate(45deg);transform:rotate(45deg);margin-right:3px}}@media only screen and (max-width: 900px) and (orientation: landscape){.app-container{padding-top:20px}h1{margin:0px;font-size:16px;color:#A3A3A7}}



